<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Cargando contenido...</span>
        </div>
    </div>
   <DeptoBase v-else
    :goal="chofer.meta_semana"
    :progress="chofer.progress"
    :name="chofer.nombre"
    :depto="chofer.depto"
    :days="chofer.dias_laborados"
    :m3="chofer.m3_persona"
    :bonus_p="chofer.bono_persona"
    :bonus_tot="chofer.bono_depto"
    :asis="chofer.asistencia"
    :extra="chofer.$_extra_m3"
    :bonus_prod="chofer.bono_productividad"
    :bonus_goals="chofer.bono_metas"
    />
</div>
</template>


<script>
import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Chofer',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("plants/getInfo", {
            department: 'choferlocal',
            userCode: this.$route.params.user,
            city: 'guadalajara'
        }); 
    },
    computed: {
        isLoading() {
            return this.$store.getters["plants/loading"];
        },
        chofer() {
        return this.$store.getters["plants/data"];
        }
    }
}
</script>